@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,500|Quicksand:400,500,700&display=swap);
.scroll {
  opacity: 0.8;
  background-color: #fe8c00;
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border-radius: 10px;
  border: none;
  outline: none;
  z-index: 8;
  cursor: pointer;
}
.scroll:focus {
  outline: none;
}
.scroll:hover {
  opacity: 1;
}

.arrow-up {
  color: white;
}

.modal-content_custom {
  width: 100vw !important;
  height: 100vh !important;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Quicksand";
  /* font-family: 'Playfair Display', ; */
}

body {
  background: white;
  color: #000;
  font-size: 15px;
  line-height: 1.5;
  letter-spacing: 1px;
  width: 100%;
}

/* END SCROLL STYLE */

a {
  color: #262626;
  text-decoration: none;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
ul {
  list-style: none;
}
.icon_camera-custom:hover {
  opacity: 1;
}

.main-content {
  width: 100%;
}

.btn {
  cursor: pointer;
  display: inline-block;
  border: 0;
  font-weight: bold;
  padding: 10px 20px;
  background-color: #fe8c00 !important;
  color: #fff !important;
  font-size: 18px;
  z-index: 2;
  border-radius: 5px;
}

.btn-blue {
  background-color: #2193b0;
}
.btn:hover {
  opacity: 0.9;
  color: #fff;
}

.dark {
  color: #ffffff !important;
}

.dark .btn {
  background: #f4f4f4;
  color: #333;
}

.inverted {
  border-color: #000;
}

/* Follow */

.follow {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 30px 0 30px;
}

.follow * {
  margin-right: 10px;
}

/* INPUT */

.input-custom {
  width: 100%;
  height: 38px;
  padding: 0.5em;
  border: 1px solid rgba(34, 36, 38, 0.15);
  cursor: pointer;
  position: relative;
  outline: 0;
  visibility: visible;
  text-align: left;
  font-weight: 700;
  letter-spacing: "1px";
}

select {
  padding: 3px 5px;
  width: auto;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  outline: none;
}

/* Accordion */

.navlink-active {
  background: #2980b9;
  color: white;
}

/* CUSTOM MODAL */
.modal-content2 {
  height: 300px !important;
}
.modal-content2::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

.modal-content2::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}
.modal-content2::-webkit-scrollbar-thumb {
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    color-stop(0.44, rgb(122, 153, 217)),
    color-stop(0.72, rgb(73, 125, 189)),
    color-stop(0.86, rgb(28, 58, 148))
  );
}
/* END CUSTOM MODAL */

/* CALENDAR */

.datepicker {
  padding: 10px;
  width: auto;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  outline: none;
}
.calendar_large .react-datepicker__day-name,
.calendar_large .react-datepicker__day,
.calendar_large .react-datepicker__time-name {
  width: 3rem !important;
  line-height: 3rem !important;
}
/* END CALENDAR */

/* MEDIA QUERY */

@media (max-width: 768px) {
  .home-cards {
    grid-template-columns: repeat(2, 1fr);
  }

  /* custom styling */
  .signin-custom,
  .signup-custom {
    grid-template-columns: 1fr !important;
  }
  .signin-left {
    height: 40vh !important;
    margin-top: 15vh !important;
  }
  .signin-right {
    height: 100% !important;
  }
}

@media (max-width: 500px) {
  .home-cards {
    grid-template-columns: 1fr;
  }
}

/* Arrows */
.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  z-index: 5;
  position: absolute;
  top: 50%;

  display: block;

  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);

  cursor: pointer;

  color: transparent;
  border: none;
  outline: none;
  background: transparent;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background: transparent;
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  /* font-family: 'slick'; */
  font-family: "FontAwesome";
  font-size: 38px;
  line-height: 1;

  opacity: 0.75;
  color: #dbd2d2;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: 25px;
}
[dir="rtl"] .slick-prev {
  right: 25px;
  left: auto;
}
.slick-prev:before {
  content: "\f104";
}
[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: 25px;
}
[dir="rtl"] .slick-next {
  right: auto;
  left: 25px;
}
.slick-next:before {
  content: "\f105";
}
[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  /* bottom: -25px; */
  bottom: 0;

  display: block;

  width: 100%;
  padding: 0;
  margin: 0;

  list-style: none;

  text-align: center;
}
.slick-dots li {
  position: relative;

  display: inline-block;

  width: 20px;
  height: 20px;
  margin: 0 10px;
  padding: 0;

  cursor: pointer;
}
.slick-dots li button {
  font-size: 0;
  line-height: 0;

  display: block;

  width: 20px;
  height: 20px;
  padding: 5px;

  cursor: pointer;

  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
  mix-blend-mode: difference;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}
.slick-dots li button:before {
  font-size: 38px;
  line-height: 20px;

  position: absolute;
  top: 0;
  left: 0;

  width: 20px;
  height: 20px;
  /* content: '•'; */
  content: "\00af";
  font-weight: 700;
  text-align: center;

  opacity: 0.25;
  color: #e9e4f0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
  opacity: 0.75;
  /* color: #ffffff; */
  mix-blend-mode: difference;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  -webkit-touch-callout: none;
  -khtml-user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;

  display: block;
  overflow: hidden;

  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;

  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  display: table;

  content: "";
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;

  height: 100%;
  min-height: 1px;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;

  height: auto;

  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}

